import navigation from '../../components/navigation/navigation';
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue';
export default {
	data() {
		return {
			config: {
				type: 2, //0无title，1有title
				renttype: 0,
				back: true,
			},
			type: 0,
			key: '',
			cards: [],
			loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			page: 1,
			colorstyle: ['', 'chuzucolor', 'chushoucolor', 'qiuzucolor', 'qiushoucolor', 'zhaoshiyoucolor'],
			remove1:false,
			remove2:false,
			remove:[false,false,false,false,false],
			hasHouseResource:'',
			unitType:'',
			minPrice:'',
			maxPrice:'',
			minSquare:'',
			maxSquare:'',
			location:''
		}
	},
	components: {
		navigation,
		uniLoadMore
	},
	filters: {
		filters1: function(arg) {
			if (arg == 1) {
				return '出租';
			} else if (arg == 2) {
				return '出售';
			} else if (arg == 3) {
				return '求租';
			} else if (arg == 4) {
				return '求购';
			} else if (arg == 5) {
				return '找室友';
			}
		},
	},
	onLoad(options) {
		console.log(options.type)
		console.log(options.key)
		this.config.renttype = options.type
		this.type = options.type
		if (options.key){
			this.location=options.key
		}
		this.getnewList();
	},
	onShow() {
		this.getnewList();
	},
	onPullDownRefresh: function() {
		//下拉刷新的时候请求一次数据
		this.getnewList();
	},
	onReachBottom: function() {
		this.getmorenews();
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		gotodetail(e,t){
			uni.navigateTo({
				url:'../order/detail?Id='+e +'&type='+t
			})
		},
		onKeyInput(){
			this.getnewList()
		},
		checkhashouse(e){
			if (e.detail.value == 1){
				this.remove1 = true
				this.hasHouseResource = true
			}
			else {
				this.remove2 = true
				this.hasHouseResource = false
			}
			this.getnewList()
		},
		checkunittype(e){
			this.unitType = e.detail.value
			this.remove[e.detail.value] = true
			this.getnewList()
		},
		removeBY(){
			this.remove1=false
			this.remove2=false
			this.remove=[false,false,false,false,false]
			this.hasHouseResource=''
			this.unitType=''
			this.minPrice=''
			this.maxPrice=''
			this.minSquare=''
			this.maxSquare=''
			this.location=''
			this.getnewList()
		},
		getnewList() {
			this.page = 1;
			this.loadingType = 2;
			uni.showNavigationBarLoading();
			this.$http
				.get('/api/home/getCards/', {
					type: this.type,
					page: this.page,
					hasHouseResource: this.hasHouseResource,
					unitType: this.unitType,
					minPrice: this.minPrice,
					maxPrice: this.maxPrice,
					minSquare: this.minSquare,
					maxSquare: this.maxSquare,
					location:this.location
				})
				.then(res => {
					console.log(res)
					console.log(res.data.cards.length);
					if (res.data.cards.length < 8) {
						console.log('我来了');
						this.loadingType = 2;
					} else {
						this.loadingType = 0;
					}
					this.page = 2; // 下次加载第2页
					this.cards = res.data.cards;

					uni.hideNavigationBarLoading(); //关闭加载动画
					uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
					uni.hideLoading();
				})
				.catch(err => {
					uni.hideLoading()
				})
		},
		getmorenews() {
			if (this.loadingType !== 0) {
				return false;
			}
			this.loadingType = 1;
			uni.showNavigationBarLoading(); //显示加载动画
			this.$http
				.get('/api/home/getCards/', {
					type: this.type,
					page: this.page,
					hasHouseResource: this.hasHouseResource,
					unitType: this.unitType,
					minPrice: this.minPrice,
					maxPrice: this.maxPrice,
					minSquare: this.minSquare,
					maxSquare: this.maxSquare,
					location:this.location
				})
				.then(res => {
					console.log(res)
					if (res.data.cards == null) {
						console.log(2);
						this.loadingType = 2;
						uni.hideNavigationBarLoading(); //关闭加载动画
						return;
					} else if (res.data.cards.length < 8) {
						this.loadingType = 2;
						uni.hideNavigationBarLoading(); //关闭加载动画
					} else {
						this.loadingType = 0; //将loadingType归0重置
						uni.hideNavigationBarLoading(); //关闭加载动画
					}
					this.page++;
					this.cards = this.cards.concat(res.data.cards); //将数据拼接在一起
				})
				.catch(err => {});
		}
	}
}
